import React from "react";
import html from "./Images/html-resize.png";
import css from "./Images/css-resize.png";
import js from "./Images/js-logo.png";

import "./Skills.css";

const Skills = () => {
  return (
    <div className="skillsContainer">
      <div className="skillsHeader">
        <strong>Skills</strong>
      </div>
      <div className="logoContainer">
        <div className="anotherLogoContainer">
          <img className="skillsLogo" src={html} />
          <div className="skillsText">2/5</div>
        </div>
        <div className="anotherLogoContainer">
          <img className="skillsLogo" src={css} />
          <div className="skillsText">1/5</div>
        </div>
        <div className="anotherLogoContainer">
          <img className="skillsLogo" src={js} />
          <div className="skillsText">1/5</div>
        </div>
      </div>
    </div>
  );
};

export default Skills;
