import React from "react";
import ProfileImage from "./ProfileImage";

import "./About.css";

const About = () => {
  return (
    <div className="aboutContainer">
      <div className="leftComponent">
        <div className="typing">
          <div className="header">
            <strong>Hi, i'm Tasha.</strong>
          </div>
          <div className="description">
            I'm a Computer Science student at Bina Nusantara University. My
            dream is to become an Astronaut someday but here i am.
          </div>
        </div>
        <div className="buttonContainer">
          <a href="mailto:vadia.sabira@yahoo.com" className="leftButtons">
            Contact Me
            <div className="mailMe">
              <p>Mail me!</p>
            </div>
          </a>
        </div>
      </div>
      <div className="rightComponent">
        <ProfileImage />
      </div>
    </div>
  );
};

export default About;
