import React from "react";

import "./Education.css";
const Education = () => {
  const Schools = props => {
    return (
      <div className="schools">
        <div className="schoolName">{props.name}</div>
        <div className="yearActive">{props.year}</div>
      </div>
    );
  };

  return (
    <div className="educationContainer">
      <div className="educationHeader">Education</div>
      <div className="educationContent">
        <Schools name="Dian Didaktika Elementary School" year="2003 - 2009" />
        <Schools name="Sumbangsih 1 Junior High School" year="2009 - 2012" />
        <Schools name="46 Senior High School" year="2012 - 2015" />
        <Schools name="Bina Nusantara University" year="2016 - 2020 (Aamiin)" />
      </div>
    </div>
  );
};

export default Education;
