import React, { useState } from "react";
import About from "./About.js";
import Skills from "./Skills.js";
import Education from "./Education.js";

import "./Content.css";

const Content = () => {
  const [activeContent, setActiveContent] = useState("about");

  const Buttons = props => {
    if (props.name === "about") {
      return <About />;
    } else if (props.name === "skills") {
      return <Skills />;
    } else if (props.name === "education") {
      return <Education />;
    } else {
      return <React.Fragment />;
    }
  };
  return (
    <div className="contentContainer">
      <div className="tabMenu">
        <button className="buttons" onClick={() => setActiveContent("about")}>
          About
        </button>
        <button className="buttons" onClick={() => setActiveContent("skills")}>
          Skills
        </button>
        <button
          className="buttons"
          onClick={() => setActiveContent("education")}
        >
          Education
        </button>
      </div>
      <div className="content">
        <Buttons name={activeContent} />
      </div>
    </div>
  );
};

export default Content;
